import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, LancamentoFaturamento } from '../../../../models/dtos/mlf/lancamentoFaturamento';
import { SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/simplesNacionalSegregacaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const LancamentoFaturamentoSearchView: FC<SearchViewProps<LancamentoFaturamento>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const empresa = globalReducer.globalParameter.empresa;

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'empresa.id',
        value: empresa.id,
      },
    ];
  }

  const columns: ColumnsProps[] = [
    referenceColumn({
      align: 'center',
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'valor',
      editable: false,
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor',
    },
    enumColumn(
      {
        dataField: 'simplesNacionalSegregacao',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Segregação',
      },
      SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES
    ),
  ];
  return (
    <WindowComponent title="Lançamentos de Faturamentos">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'LancamentoFaturamentoEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default LancamentoFaturamentoSearchView;
